import { Capacitor } from '@capacitor/core';
import { Toast } from '@capacitor/toast';
import { toast } from 'react-toastify';

/**
 * Hi·ªÉn th·ªã Toast tr√™n Android/iOS, ho·∫∑c fallback alert/console tr√™n web.
 */
export async function showToast(message: string, type: 'success' | 'error' | 'info' = 'info') {
  if (Capacitor.getPlatform() === 'web') {
    // Khi test trong Next.js (localhost, tr√¨nh duy·ªát)
    switch (type) {
      case 'success':
        await toast.success(message);
        break;
      case 'error':
        await toast.error(message);
        break;
      default:
        await toast.info(message);
    }
  } else {
    // Khi ch·∫°y tr√™n thi·∫øt b·ªã th·∫≠t (Android/iOS)
    await Toast.show({ text: message });
  }
}
